.video-card {
	picture {
		cursor: pointer;

		&:before {
			position: absolute;
			color: $primary;
			font-size: calc-em(36, 16);
			right: calc-em(20, 36);
			bottom: calc-em(20, 36);

			@include media-breakpoint-up(lg) {
				font-size: calc-em(54, 16);
				right: calc-em(35, 54);
				bottom: calc-em(35, 54);
			}
		}
	}

	img {
		border-radius: calc-em(10, 16);
	}

	&__icon-left {
		&:before {
			left: calc-em(20, 36);
			right: auto;
		}
	}

	&--play-caption {
		span {
			position: absolute;
			top: auto;
			bottom: calc-em(14, 16);
			left: calc-em(40, 16);
			font-size: calc-em(26, 16);

			@include media-breakpoint-up(lg) {
				font-size: calc-em(44, 16);
				left: calc-em(40, 16);
				bottom: calc-em(14, 16);
			}
		}
	}
}

.video-modal {
	&--body {
		padding: 0;
	}

	&--icon {
		right: calc-em(10, 16);
		top: calc-em(10, 16);
	}
}
